.t-upper {
    text-transform: uppercase;
}

.t-lower {
    text-transform: lowercase;
}

.t-cap {
    text-transform: capitalize;
}

.bg-primary,
.bg-secondary,
.bg-dark { 
    a {
        @extend .text-light;
    }
}

#content {
    a {
        text-decoration: none;
        box-shadow: none;
        color: rgba(var(--bs-link-color-rgb), 1);
     
        &:hover,
        &:focus {
            box-shadow: 0px -1.5px 0px 0px var(#{--bs-link-color}) inset;
            color: rgba(var(--bs-link-color-rgb), 0.8);
        }
    }

    .menu-item {

        a {
            &:hover,
            &:focus {
                box-shadow: none;
                color: inherit;
            }
        }
    }
}



.wp-block-quote {
    font-size: 0.85em;
    margin-left: 5%;
    border-left: 1.5px solid black;
    padding-left: 5%;
    
    cite {
        font-size: 1em;
    }
}